/**
 * æ¥å¿åå®¹åºåå
 * @param {any[]} args - éè¦åºååçåæ°æ°ç»
 * @returns {string} åºåååçå­ç¬¦ä¸²
 */
function serializeLogContent(args) {
  return args.map(arg => serializeSingleValue(arg)).join(" ");
}

/**
 * åºåååä¸ªå¼
 * @param {any} value - éè¦åºååçå¼
 * @param {Object} options - åºååéé¡¹
 * @param {number} options.maxDepth - æå¤§åºååæ·±åº¦ï¼é»è®¤ä¸º 10
 * @param {number} options.maxLength - åºååç»ææå¤§é¿åº¦ï¼é»è®¤ä¸º 10000
 * @param {string[]} options.sensitiveKeys - ææä¿¡æ¯çé®åï¼é»è®¤ä¸º ['password', 'token', 'secret']
 * @param {number} [currentDepth=0] - å½ååºååæ·±åº¦
 * @returns {string} åºåååçå­ç¬¦ä¸²
 */
function serializeSingleValue(value, options = {}, currentDepth = 0) {
  const {
    maxDepth = 10,
    maxLength = 10000,
    sensitiveKeys = ['password', 'token', 'secret', 'auth']
  } = options;
  
  // å¤ç undefined å null
  if (value === undefined) return 'undefined';
  if (value === null) return 'null';
  
  // å¤çåå§ç±»å
  const type = typeof value;
  if (
    type === 'string' || 
    type === 'number' || 
    type === 'boolean'
  ) {
    return String(value);
  }
  
  // å¤ç BigInt
  if (type === 'bigint') {
    return `${value.toString()}n`;
  }
  
  // å¤ç Symbol
  if (type === 'symbol') {
    return value.toString();
  }
  
  // å¦æå·²è¾¾å°æå¤§æ·±åº¦ï¼è¿åç±»åä¿¡æ¯
  if (currentDepth >= maxDepth) {
    return `[${Object.prototype.toString.call(value)}]`;
  }
  
  // å¤ç Error å¯¹è±¡
  if (value instanceof Error) {
    return `${value.name}: ${value.message}\nStack: ${value.stack || ''}`;
  }
  
  // å¤çæ¥æå¯¹è±¡
  if (value instanceof Date) {
    return value.toISOString();
  }
  
  // å¤çæ­£åè¡¨è¾¾å¼
  if (value instanceof RegExp) {
    return value.toString();
  }
  
  // å¤çå½æ°
  if (type === 'function') {
    const fnStr = value.toString();
    return `Function: ${value.name || 'anonymous'} ${fnStr.slice(0, 100)}${fnStr.length > 100 ? '...' : ''}`;
  }
  
  // å¤çç¹æ®éåç±»å
  if (typeof Map !== 'undefined' && value instanceof Map) {
    const obj = {};
    value.forEach((v, k) => {
      // å° Map çé®è½¬æ¢ä¸ºå­ç¬¦ä¸²
      const keyStr = typeof k === 'object' && k !== null ? '[object]' : String(k);
      obj[keyStr] = v;
    });
    return serializeSingleValue(obj, options, currentDepth + 1);
  }
  
  if (typeof Set !== 'undefined' && value instanceof Set) {
    return serializeSingleValue(Array.from(value.values()), options, currentDepth + 1);
  }
  
  // å¤çæ°ç»
  if (Array.isArray(value)) {
    const items = value.map(item => serializeSingleValue(item, options, currentDepth + 1));
    const result = `[${items.join(', ')}]`;
    return result.length > maxLength ? result.slice(0, maxLength) + '...' : result;
  }
  
  // å¤ç DOM åç´  (æµè§å¨ç¯å¢)
  if (typeof window !== 'undefined' && typeof Element !== 'undefined' && value instanceof Element) {
    return `<${value.tagName.toLowerCase()}${value.id ? ` id="${value.id}"` : ''}${value.className ? ` class="${value.className}"` : ''}>`;
  }
  
  // å¤çå¯¹è±¡
  try {
    // æ£æ¥æ¯å¦æèªå®ä¹ç toJSON æ¹æ³
    if (value !== null && typeof value.toJSON === 'function') {
      return serializeSingleValue(value.toJSON(), options, currentDepth + 1);
    }
    
    // ä½¿ç¨ JSON.stringify ä½å¤çå¾ªç¯å¼ç¨åææä¿¡æ¯
    const seen = new WeakSet();
    const result = JSON.stringify(value, function(key, val) {
      // è¿æ»¤ææä¿¡æ¯
      if (sensitiveKeys.includes(key.toLowerCase())) {
        return '[ææä¿¡æ¯å·²è¿æ»¤]';
      }
      
      // å¤çå¾ªç¯å¼ç¨
      if (typeof val === 'object' && val !== null) {
        if (seen.has(val)) {
          return '[å¾ªç¯å¼ç¨]';
        }
        seen.add(val);
      }
      
      return val;
    }, 2);
    
    // æªæ­è¿é¿çç»æ
    return result.length > maxLength ? result.slice(0, maxLength) + '...' : result;
  } catch (e) {
    // JSON.stringify å¤±è´¥ï¼å°è¯ toString
    try {
      if (value !== null && typeof value.toString === 'function' && value.toString !== Object.prototype.toString) {
        return value.toString();
      }
    } catch (e2) {
      // å¦æ toString ä¹å¤±è´¥ï¼ä½¿ç¨ Object.prototype.toString
      return Object.prototype.toString.call(value);
    }
    
    // æåçå¤éæ¹æ¡
    return String(value);
  }
}

export {
  serializeLogContent,
  serializeSingleValue
}