import Pbf from 'pbf';
import { writeLogGroup } from './sls';

/**
 * å°æ¥å¿æ°ç»åºååä¸º protobuf æ ¼å¼
 * @param {Array} logs - æ¥å¿æ°ç»
 * @returns {Uint8Array} - åºåååçäºè¿å¶æ°æ®
 */
export default function logEncoder(logs) {
  if (!Array.isArray(logs)) {
    throw new Error('logs must be array!')
  }
  
  // æå»ºæ¥å¿å¯¹è±¡
  const payload = {
    Logs: logs.map(log => {
      const { time, ...rest } = log;
      
      // åå»ºæ¥å¿åå®¹
      const logPayload = {
        Time: Math.floor(time / 1000),
        Contents: Object.entries(rest).map(([Key, Value]) => {
          // å°å¼è½¬æ¢ä¸ºå­ç¬¦ä¸²
          return { 
            Key, 
            Value: typeof Value === 'string' ? Value : JSON.stringify(Value) 
          };
        })
      };
      
      return logPayload;
    })
  };
  
  // åå»ºå¹¶ç¼ç æ¥å¿ç»
  const pbf = new Pbf();
  writeLogGroup(payload, pbf);
  return pbf.finish();
}
  
